@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: rgb(251, 253, 254);
  color: #003755;
  font-family: danske_textregular;
  font-weight: 400;
  line-height: 1.5;
}

/* Footer Section  styling  */
.header-color,
.footerFoo {
  background-color: rgb(0, 55, 85) !important;
  color: white !important;
}

/* styling paymentMethods  */

.paymentMethods {
  width: 25rem;
  border-radius: 45px;
  background-position: left top;
  background-repeat: repeat;
  padding: 20px;
}

.shippingModes {
  border-radius: 25px;
  background: url(https://upload.wikimedia.org/wikipedia/commons/thumb/b/b7/Flag_of_Europe.svg/350px-Flag_of_Europe.svg.png);
  padding: 20px;
  font-family: Georgia, "Times New Roman", Times, serif;
}

.cardgrp {
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 1%;
  margin-bottom: 1%;
}

.button {
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
  width: 100%;
}

.button {
  background-color: white;
  color: black;
  border: 2px solid #008cba;
}

.button:hover {
  background-color: white;
  color: black;
  border: 2px solid #008cba;
}

.button:hover {
  background-color: #008cba;
  color: white;
}

.contactInfo {
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 3%;
  margin-bottom: 3%;
}

.employeeCards {
  padding: 0.5%;
  grid-column-gap: 50px;
  border-radius: 15px;
  display: flex;
  transition: 0.4s ease-out;
  box-shadow: 0px 7px 10px rgba(black, 0.5);
  padding: 2%;
  margin-left: 10%;
  margin-right: 7%;
  background-color: #d5dbdb;
}

.employeeCards:hover {
  transform: translateY(20px);
}

.blockquote {
  background: #f9f9f9;
  border-left: 3px solid #ccc;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
  font-family: Georgia, "Times New Roman", Times, serif;
  margin-left: 10%;
  margin-right: 8%;
}
.blockquote:before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}

